import service from '../../service.js';
export default {
	data() {
		return {
			tabs: [{
					tit: '待审核',
				},
				{
					tit: '已通过'
				},
				{
					tit: '驳回'
				}
			],
			current: 0,
			col: {
					col: '#FA9C22',
					theme: '待审核'
				},
				loadshow:true,
				page:0,
				pages:1,
				type:1,
				listsss:[]
		}
	},
	methods: {
		jum(e){
			uni.navigateTo({
				url:e
			})
		},
		change(i) {
			this.current = i;
			this.loadshow = true;
			this.listsss = [];
			this.page = 0;
			this.pages = 1;
			const col = [{
					col: '#FA9C22',
					theme: '待审核'
				},
				{
					col: '#1AB560',
					theme: '已通过'
				},
				{
					col: '#E4393D',
					theme: '驳回'
				}
			];
			this.col = col[i];
			this.type = i+1;
			this.loadData();
			console.log(this.type);
			console.log(this.loadshow);
		},
		loadData(){
		  service.loadthepage({
				url:service.api.record.withdrawRecord,
